.gilui-nav-more {
    content: "";
    width: 0;
    height: 0;
    border-style: solid dashed dashed;
    border-color: #fff transparent transparent;
    overflow: hidden;
    cursor: pointer;
    transition: all .2s;
    -webkit-transition: all .2s;
    position: absolute;
    top: 50%;
    right: 3px;
    margin-top: -3px;
    border-width: 6px;
    border-top-color: hsla(0, 0%, 100%, .7)
}

.gilui-nav-itemed>.gilui-nav-more {
    margin-top: -9px;
    border-style: dashed dashed solid;
    border-color: transparent transparent #fff;
}

.gilui-nav * {
    font-size: 14px;
}

.gilui-nav-item {
    position: absolute;
    /* position: relative; */
    /* display: inline-block; */
    /* vertical-align: middle; */
    /* float: right; */
    display: block;
    line-height: 60px;
    top: 20px;
    right: 10px;
}

h2 {
    position: relative;
}


